* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    position: relative;
    background-color: #2f2c2f;
}

span {
    display: inline-block;
    position: absolute;
    width: 40px;
    height: 40px;
    background-color: rgb(0, 0, 0);
    cursor: pointer;
    border-radius: 3px;
    text-align: center;
    padding-top: 4px;
    opacity: 0.5;
}
span i {
    color: #fff;
    font-size: 30px;
}

span:first-of-type{
    left: 10vw;
}
span:last-of-type {
    right: 10vw;
}
span:hover {
    opacity: 0.9;
    color: #fff;
    background-color: transparent;   
}

.container img {
    max-width: 100% ;
    max-height: 100%;
    border-radius: 3px; 
    box-shadow: 1px 2px 60px 0px #fff;
    transition: all 0.5s;
}